.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
html {
  background-image: repeating-linear-gradient(-45deg, #23B8C2,#FBA43B);
  background-repeat: repeat-y;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.smallIcon{
  transform: scale(0.125);
  display: inline-block;
  margin-left: -460px;
}
.smallIcon-login{
  transform: scale(0.25);
  display: inline-block;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  text-align: center;
  margin-top: -245px;
}